@font-face {
  font-family: FuturaNormal;
  src: url("../fonts/futura_normal.ttf");
}

@font-face {
  font-family: FuturaBold;
  src: url("../fonts/futura_bold.otf");
}

@font-face {
  font-family: HelloWorld;
  src: url("../fonts/hello_world.ttf");
}

.h1 {
  font-size: 4vw;
  line-height: 3vw;
  letter-spacing: 0.03vw;
}

.h2 {
  font-size: 2.4vw;
  margin: 0;
  color: white;
  letter-spacing: 0.2vw;
}

.h3 {
  position: absolute;
  left: 200%;
  top: 50%;
  transform: translateY(-50%);
  font-size: 2vw;
  margin: 0;
  color: white;
  font-family: FuturaBold, sans-serif;
  letter-spacing: 0.3vw;
}

.h4 {
  font-size: 1vw;
  margin: 0.3vw 0 0.5vw 0;
  color: white;
  font-family: FuturaBold, sans-serif;
  letter-spacing: 0.1vw;
}

.h5 {
  font-size: 1.3vw;
  margin: 0;
  color: black;
  font-family: FuturaBold, sans-serif;
  letter-spacing: 0.03vw;
}

.h6 {
  font-size: 0.6vw;
  line-height: 1vw;
}

.p {
  line-height: 2vw;
  font-size: 1vw;
  letter-spacing: 0.03vw;
}

.p .bold {
  font-size: 1.1vw;
  letter-spacing: 0.03vw;
}

.bold {
  font-family: FuturaBold, sans-serif;
  letter-spacing: 0.07vw;
  font-size: 6vw;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

html,
body {
  font-family: FuturaNormal, sans-serif;
}

.center-column {
  max-width: 85vw;
  margin-left: 15vw;
}

.right-column {
  position: fixed;
  width: 50%;
  top: 3vw;
  right: 3vw;
  bottom: 3vw;
  overflow: hidden;
}

.section {
  width: 80%;
  display: flex;
  width: 100%;
  min-height: 100vh;
  align-items: center;
}

.section-content-wrap {
  max-width: 20vw;
}

.frame {
  border: 3vw solid #ffd800;
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  pointer-events: none;
}

.slide {
  opacity: 0;
  visibility: hidden;
  height: 100%;
  transition: all 1000ms ease-in-out;
  position: absolute;
  left: 100%;
  width: 100%;
}

.slide.active {
  opacity: 1;
  visibility: visible;
  left: 0;
  transition: all 1000ms ease-in-out;
  transition-delay: 500ms;
}

.slide.slide-about {
  background-color: #e86850;
  display: flex;
  align-items: center;
  justify-content: center;
}

.slide.slide-exp {
  background-color: #587498;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.slide.slide-skills {
  background-color: #587058;
  padding: 4vw;
  color: white;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.nav-dot {
  width: 0.5vw;
  height: 0.5vw;
  border-radius: 50%;
  background-color: black;
  position: absolute;
  left: -1.3vw;
  top: 1.75vw;
  transition: top 300ms ease-in-out;
}

.nav {
  position: fixed;
  top: 50%;
  transform: translateY(-50%);
  left: 5vw;
}

.nav-list {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

.nav-item {
  font-size: 1.5vw;
  padding: 0;
  margin: 1vw 0;
  letter-spacing: 0.1vw;
}

.nav-item:hover {
  opacity: 0.75;
}

.nav-item a {
  text-decoration: none;
  color: black;
}

.nav-item.active {
  font-family: FuturaBold, sans-serif;
}

.section-hello {
  display: flex;
  align-items: center;
}

.hero-text-wrap {
  position: relative;
  z-index: 2;
}

.hello-img {
  position: absolute;
  bottom: 0;
  left: 0;
  min-width: 100%;
  min-height: 75vh;
}

.axis-wrap {
  position: relative;
}

.axis-stripe {
  width: 0.5vw;
  height: 75vh;
  background-color: white;
  background-image: linear-gradient(#e86850 0%, white 10%);
}

.outside-circle {
  background-color: white;
  width: 2vw;
  height: 2vw;
  border-radius: 50%;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 0;
  border: 0.3vw solid #e86850;
}

.outside-circle.outside-circle-1 {
  bottom: 0;
}

.outside-circle.outside-circle-1 .h5 {
  font-family: HelloWorld;
  letter-spacing: 0.05vw;
}

.outside-circle.outside-circle-2 {
  bottom: 10%;
}

.outside-circle.outside-circle-3 {
  bottom: 30%;
}

.outside-circle.outside-circle-4 {
  bottom: 60%;
}

.outside-circle.outside-circle-5 {
  bottom: 80%;
}

.inside-circle {
  width: 0.5vw;
  height: 0.5vw;
  background-color: #e86850;
  position: absolute;
  left: 50%;
  top: 50%;
  border-radius: 50%;
  transform: translate(-50%, -50%);
}

.date-desc-wrap {
  position: absolute;
  right: 200%;
  top: 50%;
  width: 30vw;
  text-align: right;
  color: white;
  transform: translateY(-50%);
}

.job-logo-img {
  margin-bottom: 0.3vw;
  max-width: 4vw;
}

.job-logo-img.sap {
  max-width: 4.5vw;
}

.all-job-photos-wrap {
  height: 80vh;
  position: relative;
}

.job-img-wrap {
  padding: 1vw;
  background-color: white;
  max-width: 35vh;
  display: block;
  position: relative;
  transition: all 300ms ease-in-out;
}

.job-img-wrap:hover {
  background-color: black;
}

.job-img-wrap.job-img-wrap-2 {
  top: -10vh;
  left: 25%;
}

.job-img-wrap.job-img-wrap-3 {
  top: -20vh;
  right: 25%;
}

.job-img-wrap.job-img-wrap-4 {
  top: -30vh;
}

.at-job-img {
  max-width: 100%;
  display: block;
}

.logos-wrap {
  margin: 2vw 0;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}

.single-logo-wrap {
  width: 25%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 1vw 0;
  height: 5vw;
}

.logo-img {
  width: 85%;
}

.logo-img.react-native {
  width: 70%;
}

.logo-img.firebase {
  width: 70%;
}

.coding-wrap {
  display: flex;
  align-items: center;
  padding: 1vw;
  position: relative;
}

.coding-wrap:hover {
  background-color: #5f795f;
}

.coding-wrap:hover .coding-img-wrap {
  background-color: black;
}

.coding-links-wrap {
  display: flex;
}

.coding-link {
  text-decoration: none;
  margin-right: 1vw;
  margin-top: 0.5vw;
  text-decoration: none;
  font-size: 1vw;
  color: white;
  background-color: #425342;
  padding: 0.5vw 2vw;
  transition: all 300ms ease-in-out;
}

.coding-link:hover {
  background-color: black;
}

.coding-img-wrap {
  padding: 0.5vw;
  background-color: white;
  max-width: 10vw;
  margin-right: 3vw;
  transition: all 300ms ease-in-out;
}

.coding-img {
  max-width: 100%;
  display: block;
}

.contact-text-wrap {
  margin: 3vw 0;
}

.contact-img {
  position: absolute;
  top: -12vw;
  right: 0;
  width: 100%;
}

.h1-contact {
  font-size: 2.2vw;
  margin: 0;
  font-family: FuturaBold, sans-serif;
  padding: 0;
}

.h1-contact a {
  text-decoration: none;
  color: black;
  font-size: 1.5vw;
  transition: all 300ms ease-in-out;
}

.h1-contact a:hover {
  opacity: 0.75;
}

.social-wrap {
  display: flex;
  align-items: center;
}

.social-wrap a {
  text-decoration: none;
  margin-right: 1vw;
  transition: all 300ms ease-in-out;
}

.social-wrap a:hover {
  opacity: 0.75;
}

.social-wrap a img {
  max-width: 2.3vw;
}

.copyright-wrap {
  margin-top: 3vw;
}

